.heading{
	font-family: 'Grenze Gotisch', cursive;
	font-size: 2.5em;

}
.ml-10{
	margin-left: 10px;
}
.width-100{
	width: 100% !important;
	padding-left: 0px;
	padding-right: 0px;
}
.sticky{
	position: fixed;
	width: 100%;
	top: 0;
	background-color: white;
	height: fit-content;
	z-index: 10;
}
.logo{
	margin-top: 0px;
	float: left;
	position: fixed;
	left: 0px;
}

@media screen and (max-width: 500px){
	.logo{
		position: relative;
	}
}